.form {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
}
.formText {
    text-align: center;
    background-color: tomato;
    border: 1px solid black;
    padding: 16px 32px;
    color: white;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 1.334;
    letter-spacing: 0em;


}

.label {
    display: flex;
        flex-direction: column;
        margin: 0.5rem auto;
        align-items: center;
        font-size: large;
        text-align: left;
        padding: 0.5rem 1rem 0.5rem 1rem;
        border-color: black;
        border: 1px solid black;
}

.formBtn {
    margin-top: 1rem;
    margin-bottom: 2rem;
    background-color: rgba(23, 26, 32, 0.8);
    border: none;
    border-radius: 6px;
    color: #fff;
    flex-grow: 1;
    font-family: SFProText-Regular, Helvetica, Arial, sans-serif;
    font-size: 15px;
    height: 36px;
    line-height: 20px;
    margin-left: 8px;
    margin-right: 6px;
    min-width: 185px;
    padding: 0 16px 0 16px;
    color: white;
    cursor: pointer;
}

.formBtn:hover {
    background-color: rgba(23, 26, 32, 1);
    cursor: pointer;
}


.icon{
    user-select: none;
        width: 1em;
        height: 1em;
        display: inline-block;
        fill: currentcolor;
        flex-shrink: 0;
        transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        font-size: 1.5rem;
}